* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  width: 100%;
  max-width: 500px;
  border: none;
  border-radius: 5px;
  margin: 50px auto;
  padding: 10px 15px;
  font-size: 1.2em;
  display: block;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, .5);
}

.country-list {
  grid-gap: 20px;
  max-width: 1000px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  margin: 50px auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.country-list li {
  text-align: center;
  background-color: #f9f9f9;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.country-list li img {
  margin-bottom: 10px;
}

.country-info {
  max-width: 1000px;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px auto;
  display: flex;
}

.country-info img {
  border-radius: 5px;
  margin-bottom: 20px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.country-info h1 {
  margin: 0 0 10px;
  padding: 0;
  font-size: 2em;
}

.country-info p {
  margin: 0 0 5px;
  padding: 0;
  font-size: 1.2em;
}

.country-info p:last-child {
  margin-bottom: 20px;
}

.name-span {
  justify-content: center;
  font-size: 20px;
  display: flex;
}

